}

.legend-color.good { background-color: #4CAF50; }
.legend-color.okay { background-color: #8BC34A; }
.legend-color.moderate { background-color: #FFC107; }
.legend-color.low { background-color: #F44336; }

/* Table styling */
.nutrient-table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 15px;
}

.nutrient-table th, .nutrient-table td {
  padding: 8px 12px;
  text-align: left;
  border-bottom: 1px solid #ddd;
}

.nutrient-table th {
  background-color: #f2f2f2;
}

.status-optimal { background-color: #e8f5e9; }
.status-adequate { background-color: #fff8e1; }
.status-deficient { background-color: #ffebee; }

.status-indicator {
  font-weight: bold;
}

.status-optimal .status-indicator { color: #4CAF50; }
.status-adequate .status-indicator { color: #FFC107; }
.status-deficient .status-indicator { color: #F44336; }
